html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 80rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: baseline;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 4rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
  background-color: #f4f5f6;
}
.formulario .fa {
  font-size: 2rem;
  margin: 0;
  color: #1a1a1a;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.alerta {
  padding: 1rem 1rem 1rem 2rem;
  text-transform: uppercase;
  font-weight: 900;
  margin-bottom: 1rem;
  font-size: 1.2rem;
  max-width: 80rem;
}
.alerta.error {
  background-color: #ffdcdc;
  border-left: 0.5rem solid rgb(169, 0, 0);
  color: rgb(169, 0, 0);
}
.alerta.exito {
  background-color: #abffab;
  border-left: 0.5rem solid rgb(0, 120, 0);
  color: rgb(0, 120, 0);
}

.login {
  margin-top: 5rem;
}
.login .doit {
  text-align: center;
  background: linear-gradient(to right, #f59e0b 20%, #ec006a 80%);
  color: transparent;
  background-clip: text;
}
@media (min-width: 768px) {
  .login .doit {
    background: linear-gradient(to right, #f59e0b 40%, #ec006a 80%);
    color: transparent;
    background-clip: text;
    color: transparent;
    background-clip: text;
    font-size: 10rem;
    margin-bottom: 5rem;
  }
}
.login .contenedor-sm {
  background: linear-gradient(to right, rgba(245, 159, 11, 0.4) 0%, rgba(219, 39, 119, 0.4) 100%);
  backdrop-filter: blur(2rem);
  /* Efecto de desenfoque */
