  margin-bottom: 0;
}
.login input[type=submit] {
  background-color: #f59e0b;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #c57f08;
  cursor: pointer;
}
.login .acciones a {
  background: linear-gradient(to right, #cc5500 0%, #ec006a 70%);
  color: transparent;
  background-clip: text;
  font-weight: bold;
}

